"use client";

import React from "react";
import { useMobileHook } from "../useMobileView/useMobileHook";
import Local from "next/font/local";

const heroes = Local({
  src: [
    {
      path: "../../../public/fonts/Heroes Legend.ttf",
      weight: "400",
      style: "normal",
    },
  ],
  variable: "--font-heroes",
  display: "swap",
});

const superMarker = Local({
  src: [
    {
      path: "../../../public/fonts/SuperBlackMarker.ttf",
      weight: "400",
      style: "normal",
    },
  ],
  variable: "--font-superMarker",
  display: "swap",
});

const HeadBody = () => {
  const isMobile = useMobileHook();
  return (
    <>
      {isMobile ? (
        <div className="mt-24 w-full gap-2 py-2 px-1 flex flex-col items-center justify-center">
          <div>
            <h1
              className={`text-[1.3rem] tracking-wide text-amber-300 space-x-3 ${heroes.className}`}>
              <span>A</span>
              <span>developer</span>
              <span>who</span>
              <span>loves</span>
            </h1>
          </div>
          <div
            className={`dropping-texts text-3xl ${superMarker.className} font-bold flex justify-center w-full tracking-wide text-[#baf606]`}>
            <div>Web Development</div>
            <div>Software Development</div>
            <div>Game Development</div>
            <div>Problem Solving</div>
          </div>
        </div>
      ) : (
        <div></div>
      )}
    </>
  );
};

export default HeadBody;
